import { DataResolverService } from './data-resolver.service';
import { NgModule } from '@angular/core';
import { PreloadAllModules, RouterModule, Routes } from '@angular/router';
import { AuthService } from './SERVICES/auth.service';


const routes: Routes = [
  {
    path: '',
    redirectTo: 'start',
    pathMatch: 'full'
  },
  {
    path: 'tabs',
    loadChildren: () => import('./PAGES_ASSO/tabs/tabs.module').then(m => m.TabsPageModule)
  },
  {
    path: 'welcome',
    loadChildren: () => import('./SERVICES/welcome/welcome.module').then( m => m.WelcomePageModule)
  },
  {
    path: 'asso-register',
    loadChildren: () => import('./PAGES_ASSO/asso-register/asso-register.module').then( m => m.AssoRegisterPageModule)
  },
  {
    path: 'croplogo',
    loadChildren: () => import('./CROP/croplogo/croplogo.module').then( m => m.CroplogoPageModule)
  },
  
  {
    path: 'asso-edit-profile',
    loadChildren: () => import('./PAGES_ASSO/asso-edit-profile/asso-edit-profile.module').then( m => m.AssoEditProfilePageModule),
    canActivate: [AuthService]
  },
  {
    path: 'asso-login',
    loadChildren: () => import('./PAGES_ASSO/asso-login/asso-login.module').then( m => m.AssoLoginPageModule)
  },
  {
    path: 'croplogoedit',
    loadChildren: () => import('./CROP/croplogoedit/croplogoedit.module').then( m => m.CroplogoeditPageModule)
  },
  {
    path: 'croplogopost',
    loadChildren: () => import('./CROP/croplogopost/croplogopost.module').then( m => m.CroplogopostPageModule)
  },
  {
    path: 'asso-map',
    loadChildren: () => import('./PAGES_ASSO/asso-set-marker/asso-map.module').then( m => m.AssoMapPageModule)
  },
  {
    path: 'detailevents/:id',
    loadChildren: () => import('./PAGES_ASSO/detailevents/detailevents.module').then( m => m.DetaileventsPageModule)
  },
  {
    path: 'bene-register',
    loadChildren: () => import('./PAGES_BENE/bene-register/bene-register.module').then( m => m.BeneRegisterPageModule)
  },
  {
    path: 'bene-tabs',
    loadChildren: () => import('./PAGES_BENE/bene-tabs/bene-tabs.module').then( m => m.BeneTabsPageModule)
  },
  {
    path: 'start',
    loadChildren: () => import('./start/start.module').then( m => m.StartPageModule),
    canActivate: [AuthService]
  },
  {
    path: 'bene-edit-compte',
    loadChildren: () => import('./PAGES_BENE/bene-edit-compte/bene-edit-compte.module').then( m => m.BeneEditComptePageModule)
  },
  {
    path: 'cropbene',
    loadChildren: () => import('./CROP/cropbene/cropbene.module').then( m => m.CropbenePageModule)
  },
  {
    path: 'bene-details/:id',
    loadChildren: () => import('./PAGES_BENE/bene-details/bene-details.module').then( m => m.BeneDetailsPageModule),
    canActivate: [AuthService]
  },
  {
    path: 'slider',
    loadChildren: () => import('./PAGES_BENE/slider/slider.module').then( m => m.SliderPageModule)
  },


  
];
@NgModule({
  imports: [
    RouterModule.forRoot(routes, { preloadingStrategy: PreloadAllModules })
  ],
  exports: [RouterModule]
})
export class AppRoutingModule {}
